 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-hide-or-toggle-simple-hide {
	border:0;      		
	text-align: left;
	
	&.is-style-default{
		padding: 1em;
		margin: 1em;	
	}

	&.is-style-expanded{
		background-color: azure;
		border-radius: 1px;
		padding: 1.5em;
		margin: 1em;	
	}
	&.is-style-compact{
		background-color: whitesmoke;
		border-radius: 5px;
		padding: 1px;
		margin: 1px;
	
	}
}

.wp-block-hide-or-toggle-simple-hide .show {	
	cursor: pointer;
	float: left;
	margin-left: 1em;
	margin-right: 1em;
	padding-top: 3px;

}
	
.wp-block-hide-or-toggle-simple-hide .hide {
	cursor: pointer;
	display:none;
	float: left;
	margin-left: 1em;
	padding-top: 3px;
}

.wp-block-hide-or-toggle-simple-hide .title{
	color: inherit;
	margin-left: 1em;
	margin-right: 1em;
	margin-top:1px;	
}

.wp-block-hide-or-toggle-simple-hide .hidden  {
	color: inherit;
	border-radius: 1px;
	border-style: dashed;
	border-width: 1px;
	display: none; /* here is none!!! */
	margin: 1em;
	padding: 1em;
}
